@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
*{box-sizing:0;margin:0;margin:0}a{text-decoration:none}body{margin:0;font-family:'Ubuntu', sans-serif;-webkit-font-smoothing:antialiased;background-color:#282c28;color:white;max-width:1100px;margin:auto}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{color:lightgreen}

.App{text-align:left}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}.win-card{background-color:lightgreen}.loss-card{background-color:#f32f2f}.link-to{-webkit-text-decoration-color:#4f4fff;text-decoration-color:#4f4fff}a{color:#0b003d}

.MatchSmallCard .win-card{background-color:lightgreen}.MatchSmallCard .loss-card{background-color:#f32f2f}.MatchSmallCard .smaillcard-link{-webkit-text-decoration-color:mediumblue;text-decoration-color:mediumblue}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem}.TeamPage .match-details-section{grid-column:span 4}.TeamPage .win-loss-section{justify-self:center;padding:40px}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center}

.YearSelector{text-align:right;font-size:1.17em;list-style-type:none;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;background-color:coral;ont-weight:bold}.YearSelector li{margin-top:10px;margin-bottom:10px}

.MatchPage{display:grid;grid-template-columns:1fr 4fr;border-color:lightseagreen;padding:10px;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}

.HomePage{border-width:2px}.HomePage .app-name{font-size:5rem;margin:2px auto;-webkit-align-self:center;align-self:center}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.HomePage .team-grid>{justify-self:none;-webkit-align-self:center;align-self:center;text-align:center}

.TeamTitle{height:170px;text-align:center;border:10px solid white;background-color:#51f32f87}

